/* Font */
@font-face {
  font-family: tawbah100;
  src: url(./assets/fonts/tawbah100.ttf);
}
@font-face {
  font-family: Charter;
  src: url(./assets/fonts/Charter\ Regular.ttf);
}


@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500;600&display=swap');

*{
  margin: 0;
}
p {font-weight: 700;}
body { -ms-overflow-style: none; scrollbar-width: none;font-family: SF Pro Display,SF Pro Icons,Helvetica Neue,Helvetica,Arial,sans-serif;background-color: #f7f7f7;}
body::-webkit-scrollbar {
  display: none;
}
.content {width: 100%;position: absolute;top:0;bottom: 0;left: 0;right: 0;margin: auto; animation: fadein 2s;-moz-animation: fadein 2s; -webkit-animation: fadein 2s; -o-animation: fadein 2s;}
/* Navbar */

nav {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  background-color: #F8F8F8;
  backdrop-filter: blur(1);
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0.5px solid #dbdbdb;
  overflow-x: auto;
}


ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

li {
  margin: 0 20px;
}

a {
  color: #1b1b1b;
  text-decoration: none;
  font-size: 15px;
  font-weight: bold;
  transition: all 0.3s ease;
}

a:hover {
  color: #f1c40f;
}

.toggle-container {
  margin-right: 20px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
}

.switch input {
  display: none;
} 

.theme {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 34px;
}

.theme:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .theme {
  background-color: #2196f3;
}

input:checked + .theme:before {  
  transform: translateX(26px);
}

.theme.round {
  border-radius: 34px;
} 

.theme.round:before {
  border-radius: 50%;
}
/* slider */

.slider {background-color: rgb(255, 255, 255); border-bottom: 0.5px solid #d6d6d6; display: flex;justify-content: center;align-items: center;text-align: center; min-height: 70vh;width: 100%;}

.text-wrapper {width: 85%;top:0;bottom: 0;left: 0;right: 0; margin: auto;margin-bottom: 100px;padding: 0px 20px 0px 20px;}

.ayah-text {font-family: tawbah100;font-size: x-large;}

.home-title {margin-bottom: 50px; font-size: 80px; line-height: 1.05;font-weight: 500;letter-spacing: -0.015em;font-family: SF Pro Display,SF Pro Icons,Helvetica Neue,Helvetica,Arial,sans-serif; margin-top: 25px; }

.translation-text { font-size: 15px;line-height: 1.3529611765;font-weight: 600;letter-spacing: -0.022em;font-family: SF Pro Text,SF Pro Icons,Helvetica Neue,Helvetica,Arial,sans-serif;text-transform: uppercase; padding-bottom: 10px;}

/* sabiqun cards */

.sabiqun-tab {background-color:  #f8f8f8; top:0;bottom: 0;left: 0;right: 0;margin: 0 auto; padding: 7% 5% 2% 5%;}

.sabiquns { background-color: #ffffff; max-width: 1200px;margin: 0 auto;display: grid;grid-gap: 1rem;  padding: 2% 2% 1% 2%; border-radius: 30px;}

.sabiqun-card {background-color: #ffffff;padding: 0px 10px 0px 10px;border-radius: 4px; border-bottom: 0.3px solid #eaeaea}

.sabiqun-header {display: flex;justify-content: space-between;align-items: center;}

.sabiqun-title, .heart-button {display: inline-block;vertical-align: middle;justify-content: space-between; text-transform: uppercase; }

.sabiqun-title {text-align: left; color: #3c3c3c; font-size: 15px; padding-bottom: 3px;  overflow: hidden;display: -webkit-box;-webkit-line-clamp:1; /* number of lines to show */line-clamp: 1; -webkit-box-orient: vertical;}
.sabiqun-card:hover .sabiqun-title {color: #000000}

.heart-button {text-align: right; padding: 0px 4px 0px 4px;}

.scroll-top-btn {position: fixed; bottom: 0; right: 0; padding: 0px 7px 20px 0px; max-width: 60px;}


/* Sabiqun Detail */
.detail-image{
  width: 70%;
  height: 50%;
  padding: 10px 0px 10px 0px; 
}

.sabiqun-detail-page {
  padding-top: 70px;
  width: 85%;top:0;bottom: 0;left: 0;right: 0; margin: auto;
  font-family: Charter;
}

@media (min-width: 600px) {.sabiquns { grid-template-columns: repeat(2, 1fr); }}
@media (min-width: 900px) {.sabiquns { grid-template-columns: repeat(3, 1fr); }}
@media (max-width: 1123px) {.sabiqun-title{font-size: 13px;} .home-title {font-size: xx-large;} .ayah-text{font-size: medium;} .translation-text{font-size: small;} .sabiqun-detail-page{width: 93%;}}

/* animations */
@keyframes fadein {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}
@-moz-keyframes fadein { /* Firefox */
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}
@-webkit-keyframes fadein { /* Safari and Chrome */
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}
@-o-keyframes fadein { /* Opera */
  from {
      opacity:0;
  }
  to {
      opacity: 1;
  }
}